<template>
<div>
  <!-- 面包屑导航 -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>权限管理</el-breadcrumb-item>
    <el-breadcrumb-item>角色列表</el-breadcrumb-item>
  </el-breadcrumb>
  <!-- 卡片视图区 -->
  <el-card class="box-card">
    <el-button type="primary" @click="addDialogVisible = true">添加角色</el-button>
    <el-table row-key="id" :data="RolesList" border stripe>
      <!-- 展开列 -->
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1,i1) in scope.row.children"
          :key="item1.id">
            <!-- 渲染一级权限 -->
            <el-col :span="5">
              <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
              <i class="el-icon-caret-right"></i>
            </el-col>
            <!-- 渲染二级权限 -->
            <el-col :span="19">
              <el-row :class="[i2 === 0 ? '' : 'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                <el-col :span="6">
                  <el-tag closable type="warning" @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <!-- 渲染三级权限 -->
                <el-col :span="18">
                  <el-tag closable type="danger" v-for="(item3) in item2.children"
                  :key="item3.id" @close="removeRightById(scope.row, item3.id)">{{item3.authName}}</el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
      <!-- 索引列 -->
       <el-table-column label="#" type="index"></el-table-column>
       <el-table-column label="角色名称" prop="roleName"></el-table-column>
       <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
       <el-table-column label="操作" prop="name">
         <template slot-scope="scope">
           <el-button type="primary" icon="el-icon-edit" size="small" @click="showEditDialog(scope.row.id)">编辑</el-button>
           <el-button type="primary" icon="el-icon-delete" size="small" @click="removeRoleById(scope.row.id)">删除</el-button>
           <el-button type="primary" icon="el-icon-setting" size="small" @click="showSetRigthDialog(scope.row)">分配权限</el-button>
         </template>
       </el-table-column>
    </el-table>
  </el-card>
  <!-- 分配权限对话框 -->
  <el-dialog
    title="分配权限"
    :visible.sync="setRightDialogVisible"
     @close="setRightDialogClosed"
    width="50%">
    <!-- 树型控件展示信息 -->
    <el-tree :data="setRightsForm" :props="treeProps"
     show-checkbox node-key="id" default-expand-all
     :default-checked-keys="defKeys" ref="treeRef" ></el-tree>
    <span slot="footer" class="dialog-footer">
      <el-button @click="setRightDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="allotRights">确 定</el-button>
    </span>
  </el-dialog>
  <!-- 添加角色对话框区域 -->
  <el-dialog
    title="角色添加"
    :visible.sync="addDialogVisible"
    @close="addDialogClosed"
    width="40%">
    <el-form :model="RolesForm" :rules="RolesFormRules" ref="RolesFormRef" label-width="100px" class="demo-ruleForm">
      <el-form-item label="角色名称" prop="roleName">
        <el-input v-model="RolesForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="角色描述" prop="roleDesc">
        <el-input v-model="RolesForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="addDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="addRolesOk">确 定</el-button>
    </span>
  </el-dialog>
  <el-dialog
    title="角色修改"
    :visible.sync="editDialogVisible"
    @close="editDialogClosed"
    width="40%">
    <el-form :model="editRolesForm" :rules="RolesFormRules" ref="editRolesFormRef" label-width="100px" class="demo-ruleForm">
      <el-form-item label="角色名称" prop="roleName">
        <el-input v-model="editRolesForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="角色描述" prop="roleDesc">
        <el-input v-model="editRolesForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="editDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="aeditRolesOk">确 定</el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  data () {
    return {
      RolesList: [],
      addDialogVisible: false,
      editDialogVisible: false,
      setRightDialogVisible: false,
      editRolesForm: { },
      // 树型所有权限信息
      setRightsForm: { },
      defKeys: [],
      // 保存ID值
      roleId: '',
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      RolesForm: {
        roleName: '',
        roleDesc: ''
      },
      RolesFormRules: {
        roleName: { required: true, message: '请输入角色名', trigger: 'blur' },
        roleDesc: { required: true, message: '请输入描述', trigger: 'blur' }
      }
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取角色列表信息
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.RolesList = res.data
    },
    // 关闭时清空表单信息
    editDialogClosed () {
      this.$refs.editRolesFormRef.resetFields()
    },
    addDialogClosed () {
      this.$refs.RolesFormRef.resetFields()
    },
    // 获取所有权限信息
    async showSetRigthDialog (role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('获取权限信息失败')
      }
      this.setRightsForm = res.data
      // 把当前对应的角色权限传给树型递归勾选
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // 监听权限对话框权限的关闭事件
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 点击为角色分配权限
    async allotRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('分配权限失败！')
      }
      this.$message.success('分配权限成功！')
      this.getRolesList()
      this.setRightDialogVisible = false
    },
    // 修改角色信息之获取对应信息
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色信息失败')
      }
      this.editRolesForm = res.data
      this.editDialogVisible = true
    },
    // 修改角色信息之提交
    aeditRolesOk () {
      this.$refs.editRolesFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put('roles/' + this.editRolesForm.roleId, {
          roleName: this.editRolesForm.roleName,
          roleDesc: this.editRolesForm.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error('修改角色信息失败')
        this.$message.success('修改角色信息成功')
        this.editDialogVisible = false
        // 重新获取用户列表数据
        this.getRolesList()
      })
    },
    // 根据ID删除角色标签权限
    async removeRightById (role, rightid) {
      const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightid}`)
      if (res.meta.status !== 200) {
        return this.$message.error('权限删除失败')
      }
      this.$message.success('权限已删除')
      // 将最新权限的信息赋值给渲染变量
      role.children = res.data
    },
    // 获取三级权限的ID，保存到数组中
    getLeafKeys (node, arr) {
      // 如果当前 node 节点不包含 children 属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 删除角色信息
    async removeRoleById (id) {
      const confirmResult = await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 如果用户确认删除，则返回值为字符串 confirm
      // 如果用户取消了删除，则返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('角色删除失败')
      }
      this.$message.success('角色已删除')
      // 重新刷新列表信息
      this.getRolesList()
    },
    // 添加角色
    addRolesOk () {
      this.$refs.RolesFormRef.validate(async valid => {
        if (!valid) return
        // 调用数据库API添加角色
        const { data: res } = await this.$http.post('roles', this.RolesForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加角色失败')
        }
        this.$message.success('添加角色成功')
        // 隐藏添加用户的对话框
        this.addDialogVisible = false
        // 重新获取用户列表数据
        this.getRolesList()
      })
    }
  }
}
</script>

<style>
  .el-tag{
    margin: 7px;
  }
  .bdbottom{
    border-bottom:1px solid #eee ;
  }
  .bdtop{
    border-top:1px solid #eee ;
  }
  .vcenter{
    display: flex;
    align-items: center;
  }
</style>
