<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 权限列表 表头 -->
     <el-table :data="RightsList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="权限名称" prop="path"></el-table-column>
        <el-table-column label="路径" prop="authName"></el-table-column>
        <el-table-column label="权限等级" prop="level">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'">一级权限</el-tag>
            <el-tag type="warning" v-else-if="scope.row.level === '1'">二级权限</el-tag>
            <el-tag type="danger" v-else >三级权限</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      RightsList: []
    }
  },
  // 获取数据时用的钩子函数
  created () {
    this.getRightslist()
  },
  methods: {
    async getRightslist () {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$message.error('获取列表信息失败')
      }
      this.RightsList = res.data
    }
  }
}
</script>

